<template lang="html">
<div class="new-message col-lg-9">
  <form class="form-group" @submit.prevent="addMessage" id="el">
    <audio id="player">
      <source src="../assets/send.mp3" type="audio/mp3">
    </audio>
    <row class="col-lg-12 mt-1">
      <input class="form-control newmessage_input pl-2" placeholder="" type="text" name="new-message" id="nm" v-model="newMessage"/>
      <button type="submit" class="sendMessage ml-3"  @submit.prevent="addMessage" name="button"><i class="fab fa-telegram-plane fa-2x"></i></button>
    </row>
  </form>
</div>
</template>

<script>
import { Row, } from 'mdbvue'
export default {
  name: 'NewMessage',
  props: ['name'],
  components: {
    Row,
  },
  data(){
    return {
      newMessage: null,
      time: null,
      answer: ''
    }
  },
  mounted () {
  var databaseRef  = firebase.database().ref('messages/');
  var uid  = firebase.database().ref().child('messages').push().key;
  var databaseRef  = firebase.database().ref('messages/');
  var item = document.getElementById('item')
  var commentsRef = firebase.database().ref('messages');
  commentsRef.on('child_added', function(data) {
      var msg = data.val().message;
      var name = data.val().user;
      var ul = $(".messages");
      var gren = $('<span></span>').addClass('teal-text mr-2').text(name);
      var grey = $('<span></span>').addClass('grey-text').text(msg);
         var li = $('<li></li>').addClass('msg mb-2')
	ul.append(li.append(gren,grey))
  $( ".card_chat" ).scrollTop(1500);




    });
},
  methods: {
        addMessage() {
          var databaseRef  = firebase.database().ref('messages/');
          var uid  = firebase.database().ref().child('messages').push().key;
          var date = {
            message_id: uid,
            message: this.newMessage,
            user: this.name
          }
          var updates = {};
          updates['/messages/' + uid] = date;
          firebase.database().ref().update(updates);
          document.getElementById('player').play()

          this.newMessage = '';
          $( ".card_chat" ).scrollTop(14300);


    },
    cl (){
    }

  },
 watch: {
   newMessage: function (newnewMessage, oldnewMessage) {

 }

},
}
</script>

<style lang="css">


.messages li{
  display: flex;
	flex-flow: row;
}

.msds {
position: relative;
height: 100%;
flex-flow:column;
overflow:hidden;
}
.newmessage_input {
  width: 400px;
  border-radius: 30px;
}
.sendMessage {
  background: none;
  border: none;
  outline: none;

}
.sendMessage:active{
  outline: none;
}
.sendMessage:focus{
  outline: none;
}
@media screen and (max-width: 768px) {
  .newmessage_input {
    width: 200px;
  }
}
</style>



